@import url(https://fonts.googleapis.com/css?family=Alfa+Slab+One|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (max-width: 767px){#header{padding:1em 1em 0}section p:nth-child(2){max-width:90%}#tourism .small-image,#tourism .large-image{width:90%}#tourism p:nth-child(2){max-width:80%}#services .grid{margin-bottom:1em;margin-top:0}#services .grid li{flex-basis:100%;padding:0.4em}#tourism,#packages{padding:2.5em 0 0.5em}#services,#contact{padding:2.5em 1em 0.5em}}@media (min-width: 768px){#header{padding:1.4em 4em 0}section p:nth-child(2){max-width:60%}#tourism .small-image{width:30%}#tourism .large-image{width:70%}#services .grid{margin-bottom:0;margin-top:1em}#services .grid li{flex-basis:50%;padding:0.75em}}@media (max-width: 991px){#header nav ul{background:#00000054;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:0em 1em;border-radius:0.25em}}@media (min-width: 992px){#services .grid li{flex-basis:33.3%;padding:0.75em}}*{margin:0;padding:0;box-sizing:border-box;text-align:center;font-family:'Open Sans', sans-serif}#header,#services,#contact{background-color:#ccecff}#header,#tourism,#services,#packages{min-height:100vh;top:0;left:0;width:100%;height:100%}.btn-dark-blue{color:#fff;background-color:#193060;border-color:#fff;font-size:1em}.btn-dark-blue:hover{color:#fff;background-color:#4281a6;border-color:#fff}section{display:flex;flex-direction:column;align-items:center;padding:4em 3.5em 1em}section h2{text-transform:capitalize;font-size:2em;font-weight:bold;margin-bottom:0.4em;color:#193060}section p{line-height:1.8}section hr{width:15%;height:0.2em;background-color:#193060;border:0;margin-bottom:3em}#header{color:#fff;background:url("https://cdn.pixabay.com/photo/2017/12/01/03/17/landscape-2990060_1280.jpg") no-repeat center #6b6b6b;background-size:cover;background-blend-mode:overlay}#header nav{display:flex;width:100%;padding:0}#header nav span{font-family:'Alfa Slab One', cursive;color:#fff;-webkit-text-stroke-color:#193060;-webkit-text-stroke-width:thin}#header nav ul{margin:auto;margin-right:0}#header nav ul li a{color:#fff;float:right;font-weight:600}#header svg{color:#fff}#header .header-content{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);-webkit-animation:1s fadein 0.5s forwards;animation:1s fadein 0.5s forwards;opacity:0}#header .header-content h1{font-size:calc(0.5em + 3.5vw);margin-bottom:0.3em;font-family:'Alfa Slab One', cursive;color:#fff;-webkit-text-stroke-color:#193060;-webkit-text-stroke-width:thin}#header .header-content h5{margin-bottom:1.6em;color:#fff}@-webkit-keyframes fadein{100%{opacity:1}}@keyframes fadein{100%{opacity:1}}#tourism .images{display:flex;flex-wrap:wrap;justify-content:center}#tourism #container{position:relative}#tourism #container .overlay{opacity:0;background-color:#55555533;border-radius:0.25em;padding:1em;-webkit-transition:all .5s ease;transition:all .5s ease;color:#fff;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-align:center;text-transform:uppercase}#tourism #container:hover .overlay{opacity:1}#tourism img{list-style:none;height:260px;min-height:100%;width:100%;object-fit:cover;padding:0.5em;background-clip:content-box;background-size:cover;background-position:center}#services .grid{width:100%;display:flex;flex-wrap:wrap;justify-content:center}#services .grid li{list-style:none}#services .grid li h3{margin:0.2em 0 0.4em}#services svg{font-size:3em;margin-top:0.6em;color:#4281a6}#packages hr{margin-bottom:1.6em}#packages .cards{width:100%;margin:auto;display:flex;flex-wrap:wrap;justify-content:center}#packages .cards .card{width:18rem;margin:0.5em}#packages .cards .card img{height:12em}#packages .cards .card .card-body{padding:0.5em;background-color:#ffd938}#packages .cards .card .card-body .card-title{font-weight:bold;color:#193060}#packages .cards .card .card-body button{color:#193060;background:transparent;padding:0.15em 0.35em;margin:0.25em;border:none}#packages .cards .card .card-body button:hover{color:#fff;background-color:#193060;border-color:#193060}#contact .contact{margin:0 0 2.2em}#contact h4{margin:0 0 0.4em;color:#4281a6}#contact .form-inline{justify-content:center}#contact span{color:#193060}#contact svg{font-size:2em;margin:0 0.4em;color:#193060}#contact svg:hover{color:#4281a6}#footer{color:#fff;background-color:#193060;padding:1em}#footer p{margin:0}#footer svg{color:#fff;padding:0 0.2em 0 0;font-size:1.2em}#go-up-button{opacity:0.2;width:2em;height:2em;position:fixed;bottom:0.5em;right:0.5em;background-color:transparent;border:none}#go-up-button svg{font-size:2em}#go-up-button:hover{opacity:1}.modal-header{display:block}.modal-header .modal-title{font-weight:600;color:#193060}.modal-body{padding-bottom:0}.modal-body h5{margin-bottom:0.1em;font-weight:400}.modal-body li{list-style:none;font-weight:100}.modal-footer{padding:0.5em}.show{display:block;background-color:#000000b5;overflow:auto}.swal-button{color:#193060;background:transparent;padding:0.15em 0.35em;margin:0.25em;border:none;font-size:1.4rem}.swal-button:not([disabled]):hover{color:#fff;background-color:#193060;border-color:#193060}

